  methods: {
    // 分页器方法
    handleSizeChange (val) {
      this.currentPage = 1;
      this.pageSize = val;
    },
    //当前页改变时触发 跳转其他页val为传入的当前页码
    handleCurrentChange (val) {
      this.currentPage = val;
      this.tableDatas = this.tableData.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      )
    },
    // handleEdit (index, row) {
    //   console.log(index, row);
    // },
    // handleDelete (index, row) {
    //   console.log(index, row);
    // },

    handleClick (row) {
      this.mapa.centerAndZoom(row.point, 15);
      this.$notify({
        title: row.name,
        message: '电话：' + row.phone,
        iconClass: 'el-icon-map-location',
        duration: 0
      });
    },
    resetDateFilter () {
      this.$refs.filterTable.clearFilter('km');
    },
    clearFilter () {
      this.$refs.filterTable.clearFilter();
    },
    formatter (row, column) {
      return row.address;
    },
    filterTag (value, row) {
      return row.tag === value;
    },
    filterHandler (value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },



    createMap () {

      var map = new BMapGL.Map('map');
      this.mapa = map;
      map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
      var navi3DCtrl = new BMapGL.NavigationControl3D();  // 添加3D控件
      map.addControl(navi3DCtrl);

      // 创建城市选择控件
      var cityControl = new BMapGL.CityListControl({
        // 控件的停靠位置（可选，默认左上角）
        anchor: BMAP_ANCHOR_TOP_LEFT,
        // 控件基于停靠位置的偏移量（可选）
        offset: new BMapGL.Size(10, 5)
      });
      // 将控件添加到地图上
      map.addControl(cityControl);
      //目前还有问题
      // 创建定位控件
      var locationControl = new BMapGL.LocationControl({
        // 控件的停靠位置（可选，默认左上角）
        anchor: BMAP_ANCHOR_TOP_RIGHT,
        // 控件基于停靠位置的偏移量（可选）
        offset: new BMapGL.Size(20, 20)
      });
      // 将控件添加到地图上
      map.addControl(locationControl);
      // 添加定位事件
      locationControl.addEventListener("locationSuccess", function (e) {
        var address = '';
        address += e.addressComponent.province;
        address += e.addressComponent.city;
        address += e.addressComponent.district;
        address += e.addressComponent.street;
        address += e.addressComponent.streetNumber;
        alert("当前定位地址为：" + address);
      });
      locationControl.addEventListener("locationError", function (e) {
        alert(e.message);
      });
      //目前还有问题
      //初始化地图初始位置
      var point = new BMapGL.Point(116.404, 39.925);
      map.centerAndZoom(point, 15);



      //创建点标记
      var marker = new Array(this.tableData.length)
      // var infoWindow = new Array(this.ditushuju.length)
      for (var i = 0; this.tableData.length > i; i++) {
        marker[i] = new BMapGL.Marker(this.tableData[i].point);
        map.addOverlay(marker[i]);
      }
    },
    // dian(i) {
    //          // this.mapa.openInfoWindow(infoWindow[i], this.ditushuju[i].point); // 开启信息窗口
    //           this.mapa.openInfoWindow(infoWindow[i], this.ditushuju[i].point); // 开启信息窗口
    //         }
    // dian (i) {
    //   // console.log(this.ditushuju.length);
    //   var marker = new BMapGL.Marker(this.ditushuju[i].point);
    //   // console.log(this.ditushuju[i].point);
    //   console.log(marker);
    //   this.mapa.addOverlay(marker);
    //   //创建信息窗口
    //   var infoWindow = new BMapGL.InfoWindow(this.ditushuju[i].miaoshu, this.ditushuju[i].opts);
    //   // 点标记添加点击事件
    //   marker.addEventListener('click', function () {
    //     this.mapa.openInfoWindow(infoWindow, this.ditushuju[i].point); // 开启信息窗口
    //   });
    // }


    // sa () {
    //   var pointa = new BMapGL.Point(60, 39.925);
    //   console.log(this.pointa)

    //   this.mapa.centerAndZoom(this.pointa[2], 15);
    // }
  },
  mounted () {
    this.tableDatas = this.tableData.slice(0, 6)
    this.createMap()
